import React from 'react';

import utils from '../../utils';

import axios from 'axios';

import './style.css';

const options = [
  utils.newArray(6).map(i => {
    return utils.localize(`inquiry.options1_${i + 1}`);
  }),
  utils.newArray(10).map(i => {
    return utils.localize(`inquiry.options2_${i + 1}`);
  }),
  utils.newArray(7).map(i => {
    return utils.localize(`inquiry.options3_${i + 1}`);
  })
];

const formRows = utils.newArray(3).map(i => {
  return {
    label: utils.localize(`inquiry.label${i + 1}`),
    sendLabel: utils.localize(`inquiry.send.label${i + 1}`),
    options: options[i]
  };
});

const mailAddress = 'wb@leadingidea.net';

class Inquiry extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      selectValues: [0, 0, 0],
      inputValue1: '',
      inputValue2: '',
      enableButton: true
    };

    this.handleSelectChange = this.handleSelectChange.bind(this);
    this.handleInput1Change = this.handleInput1Change.bind(this);
    this.handleInput2Change = this.handleInput2Change.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSelectChange(e) {
    const newValues = [...this.state.selectValues];
    newValues.splice(e.target.name, 1, e.target.value);
    this.setState({
      selectValues: newValues
    });
  }

  handleInput1Change(e) {
    this.setState({
      inputValue1: e.target.value
    });
  }

  handleInput2Change(e) {
    this.setState({
      inputValue2: e.target.value
    });
  }

  handleSubmit() {
    const {selectValues, inputValue1, inputValue2} = this.state;
    if (inputValue1.trim() === '' || inputValue2.trim() === '') {
      alert(utils.localize('inquiry.error'));
      return;
    }
    this.setState({
      enableButton: false
    });
    let postData = selectValues.map((value, i) => {
      return {
        field: formRows[i].sendLabel,
        value: options[i][value]
      };
    });
    postData.push({
      field: utils.localize('inquiry.send.label4'),
      value: inputValue1 + ' ' + utils.localize('inquiry.yuan.per.year')
    }, {
      field: utils.localize('inquiry.send.label5'),
      value: inputValue2
    });

    let mailBody = postData.map(d => d.field + ': ' + d.value).join('\n');
    axios.post('/sendMail', {
      to: mailAddress,
      content: mailBody
    }).then(result => {
      this.setState({
        enableButton: true
      });
      alert(utils.localize('inquiry.success'));
    });
  }

  render() {
    return (
      <div className="inquiry">
        <div className="background-container">
          <h1>{utils.localize('inquiry.title')}</h1>
          <p>{utils.localize('inquiry.content')}</p>
        </div>
        <div className="content-container">
          {
            formRows.map((formRow, i) => {
              return (
                <div className="form-row" key={i}>
                  <label>{formRow.label}: </label>
                  <select name={i} onChange={this.handleSelectChange} value={this.state.selectValues[i]}>
                    {
                      formRow.options.map((option, j) => {
                        return <option value={j} key={`option${j}`}>{option}</option>;
                      })
                    }
                  </select>
                </div>
              );
            })
          }
          <div className="form-row">
            <label>{utils.localize('inquiry.label4')}:</label>
            <input value={this.state.inputValue1} onChange={this.handleInput1Change} placeholder={utils.localize('inquiry.placeholder4')} type="number" />
            <span>{utils.localize('inquiry.yuan.per.year')}</span>
          </div>
          <div className="form-row">
            <label>{utils.localize('inquiry.label5')}:</label>
            <input value={this.state.inputValue2} onChange={this.handleInput2Change} placeholder={utils.localize('inquiry.placeholder5')} type="tel" />
          </div>
        </div>
        <div className={`submit-button ${this.state.enableButton ? '' : 'disable-button'}`} onClick={this.state.enableButton ? this.handleSubmit : null}>{utils.localize('inquiry.submit')}</div>
      </div>
    );
  }
}

export default Inquiry;
