import React from 'react';

import utils from '../../utils';

const lang = window.localStorage.getItem('leading_lang');

import './style.css';

const items = utils.newArray(6).map(i => {
  return {
    src: require(`../../sources/aboutus/view6_${i + 1}.jpg`).default,
    icon: require(`../../sources/aboutus/view6_${i + 1}.png`).default,
    title: utils.localize(`view6.img.title${i + 1}`),
    content: utils.localize(`view6.content${i + 1}`),
    contentMore: i === 0 && lang !== 'en' ? utils.localize(`view6.content${i + 1}.more`) : undefined
  };
});

class View6 extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      showMore: false
    };

    this.showMore = this.showMore.bind(this);
    this.hideMore = this.hideMore.bind(this);
  }

  showMore(content, e) {
    e.stopPropagation();
    this.setState({
      showMore: true
    });
  }

  hideMore() {
    this.setState({
      showMore: false
    });
  }

  renderItem({src, icon, title, content, contentMore}) {
    return (
      <div className="view-item-container">
        <div className="view-content-text-container">
          <p onClick={this.hideMore}>
            {contentMore && this.state.showMore ? contentMore : content}
            {!this.state.showMore && contentMore ? <a onClick={this.showMore.bind(this, contentMore)}>--{utils.localize('more')}--</a> : null}
          </p>
        </div>
        <div className="view-content-img-container">
          <img src={src} />
          <div className="view-content-title-container">
            <img className="view-content-icon-container" src={icon} />
            <h2>{title}</h2>
          </div>
        </div>
      </div>
    );
  }

  render() {
    return (
      <div className="view-container view6">
        <h2 className="view6-title">{utils.localize('view6.title')}</h2>
        <div className="view-content-container">
          {
            items.map(item => this.renderItem(item))
          }
        </div>
      </div>
    );
  }
}

export default View6;