import React from 'react';

import utils from '../../utils';

import './style.css';

const contents = utils.newArray(4).map(i => {
  return {
    name: utils.localize(`view12.name${i + 1}`),
    title: utils.localize(`view12.title${i + 1}`),
    content: utils.localize(`view12.content${i + 1}`),
    src: require(`../../sources/aboutus/view12_${i + 1}.png`).default
  };
});

class View12 extends React.Component {
  constructor(props) {
    super(props);

    this.renderContent = this.renderContent.bind(this);
  }

  renderContent(content, index) {
    return (
      <div className="view-content" key={index}>
        <img src={content.src} />
        <div className="view-content-text-container">
          <div className="view-content-title">
            <h3>{content.name}</h3>
            <span>{content.title}</span>
          </div>
          <div className="view-content-content">
            <p>{content.content}</p>
          </div>
        </div>
      </div>
    );
  }

  render() {
    return (
      <div className="view-container view12">
        <h2 className="view-title">{utils.localize('view12.title')}</h2>
        <div className="view-content-container">
          {
            contents.map((c, i) => {
              return this.renderContent(c, i);
            })
          }
        </div>
      </div>
    );
  }
}

export default View12;