import React from 'react';
import Icon from 'src/components/icon';
import Layer from '../layer/index';
import utils from 'src/utils';
import { withRouter } from 'react-router-dom';
import './pageFooter.css';

class PageFooter extends React.Component {
  constructor(props) {
    super(props);

    this.jumpTo = this.jumpTo.bind(this);
  }

  jumpTo(link) {
    window.scrollTo(0, document.body.clientWidth / 2 - 100);
    this.props.history.push({pathname: link});
  }

  render() {
    return (
      <div className="page-footer">
        <div className="page-footer-content">
          <div className="footer-column-container">
            <div className="footer-logo-container">
              <img src={require('../../sources/leading_logo.png').default} />
            </div>
          </div>
          <div className="footer-column-container">
            <div className="qr-code"></div>
            <p>{utils.localize('qr1')}</p>
            <p>{utils.localize('qr2')}</p>
          </div>
          <div className="footer-column-container">
            <h2 onClick={this.jumpTo.bind(this, '/about/')}>{utils.localize('menuItem1')}</h2>
            <h2 onClick={this.jumpTo.bind(this, '/ipLicensing/')}>{utils.localize('menuItem2')}</h2>
            <h2 onClick={this.jumpTo.bind(this, '/showcase/')}>{utils.localize('menuItem3')}</h2>
          </div>
          <div className="footer-column-container last">
            <h2 onClick={this.jumpTo.bind(this, '/contact/')}>{utils.localize('menuItem4')}</h2>
            <div className="footer-info-row">
              <div>Echo Yin:</div>
              <div><Icon name="tel" />18621588879</div>
              <div><Icon name="mail" />echoyin@maxantad.cn</div>
            </div>
            <div className="footer-info-row">
              <div>Yoyo Feng:</div>
              <div><Icon name="tel" />15801934927</div>
              <div><Icon name="mail" />yoyofeng@maxantad.cn</div>
            </div>
            <div className="footer-info-row">
              {utils.localize('address')}
            </div>
          </div>
        </div>
        <div className="copyright-container">
          {utils.localize('copyright')}
          <a className="icp" href="http://icp.chinaz.com/home/info?host=leadingidea.net">ICP2020025301-1</a>
        </div>
      </div>
    );
  }
}

export default withRouter(PageFooter);