import React from 'react';

import utils from '../../utils';

import './style.css';

class View9 extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="view-container view9">
        <div className="view-content-container">
          <h2>{utils.localize('view9.title')}</h2>
          {
            utils.newArray(5).map(i => {
              return (<p>{utils.localize(`view9.content${i + 1}`)}</p>);
            })
          }
          <img src={require('../../sources/iplicensing/view9.png').default} />
        </div>
      </div>
    );
  }
}

export default View9;