import React, {Suspense, lazy} from 'react';
import ReactDOM from 'react-dom';
import utils from './utils';
import { Router, Route, Switch } from 'react-router';
import { createBrowserHistory } from 'history';
import './components/style.css';

import HomePage from './components/homePage';
import AboutUs from './components/aboutUs';
import IpLicensing from './components/ipLicensing';
import Showcase from './components/showcase';
import ContactUs from './components/contactUs';
import Inquiry from './components/inquiry';
import ShowcaseDetail from './components/showcaseDetail';
import Menu from './components/menu';
import ImgSwitcher from './components/imgSwitcher';
import PageFooter from './components/pageFooter';

const browserHistory = createBrowserHistory();

const menuLinks = ['/', '/about/', '/ipLicensing/', '/showcase/', '/contact/', '/inquiry/'];

const menuItems = utils.newArray(6).map(i => {
  return {
    name: utils.localize('menuItem' + i),
    link: menuLinks[i]
  };
});

document.title = utils.localize('companyName');

ReactDOM.render(
  <React.StrictMode>
    <Router history={browserHistory} >
      <Menu location={browserHistory.location} items={menuItems} />
      <ImgSwitcher
        autoSwitch={true}
        height={600}
      />
      <Suspense fallback={<div></div>}>
        <Switch>
          <Route exact path="/" component={HomePage} />
          <Route path="/about" component={AboutUs} />
          <Route path="/ipLicensing" component={IpLicensing} />
          <Route path="/showcase/:type" component={ShowcaseDetail} />
          <Route path="/showcase" component={Showcase} />
          <Route path="/contact" component={ContactUs} />
          <Route path="/inquiry" component={Inquiry} />
        </Switch>
      </Suspense>
      <PageFooter />
    </Router>
  </React.StrictMode>,
  document.getElementById('root')
);
